<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="3">
          <v-img
              :src="require('../../assets/icons/calculator02.svg')"
              contain
              height="90"
          />
        </v-col>

        <v-col cols="8" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title">Impuesto a la renta</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"></v-col>
      </v-row>

      <v-row>
        <v-col>
          <p class="text-center">Impuesto a la renta de segunda<br> categoría por venta de inmuebles </p>
          <p>La venta de un inmueble que efectúa una persona natural sin rentas empresariales, genera ganancia de
            capital, la cual se considera renta de segunda categoria afecta al impuesto a la Renta.</p>
          <p class="font-weight-bold">Casos en los cuales no pagas el impuesto:</p>
          <ul>
            <li>Inmuebles adquiridos antes del 01/01/2004</li>
            <li>La venta que constituya casa habitación del vendedor.</li>
            <li>El costo de adquisición es mayor o igual al precio de venta.</li>
          </ul>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <div class="call-to-action" style="margin: 1rem;">
            <p>Precio de compra del inmueble</p>
            <v-text-field
                @keypress="isNumber($event)"
                v-model.number="price.total"
                prefix="S/"
                hide-details
            ></v-text-field>
          </div>
          <div class="call-to-action" style="margin: 1rem;">
            <p>Precio de venta</p>
            <v-text-field
                @keypress="isNumber($event)"
                v-model.number="price.auto"
                prefix="S/"
                hide-details
            ></v-text-field>
          </div>
          <div class="text-center">
            <v-btn @click="onSubmit" color="primary" width="200">Calcular</v-btn>
          </div>
        </v-col>
        <v-col cols="4" align-self="center" class="text-center">Resultado</v-col>
        <v-col cols="8">
          <v-text-field
              v-model.number="tax"
              prefix="S/"
              hide-details
              readonly
          ></v-text-field>
        </v-col>
        <v-col cols="9"></v-col>
        <v-col cols="3" class="text-center" @click="goToIndex">
          <v-img
              :src="require('../../assets/icons/home-12.svg')"
              contain
              height="48"
          />
          Regresar
        </v-col>
      </v-row>

    </v-container>
  </div>
</template>

<script>
import axios from "axios"

export default {
  name: 'Calculator02',
  data: () => ({
    uit: 0,
    price: {
      total: 0,
      auto: 0,
    },
    tax: 0,
  }),
  computed: {},
  mounted() {
    this.getUITValue()
  },
  methods: {
    getUITValue() {
      let url = 'settings/1'
      axios.get(url).then((response) => {
        this.uit = parseInt(response.data.value)
      })
    },
    isNumber: function (evt) {
      evt = (evt) ? evt : window.event
      let charCode = (evt.which) ? evt.which : evt.keyCode
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault()
      } else {
        return true
      }
    },
    onSubmit() {
      this.tax = (this.price.auto - this.price.total) * 0.05;

      if (this.tax <= 0) {
        this.tax = 'No se encuentra afecto';
      }
    },
    goToIndex() {
      this.$router.push({name: 'CalculatorIndex'})
    }
  }
}
</script>
